$width: 10vw;

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.screen {
  width: 100vw;
  height: 100vh;
  background-color: #8BC6EC;
  background-image: linear-gradient(180deg, #8BC6EC 0%, #9599E2 50%);
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.title-container {
  margin: 10vh;
}

.title {
  font-size: 5vh;
  font-weight: 700;
  text-transform: uppercase;
  color: white;
}

.subtitle {
  font-size: 4vh;
  font-weight: 400;
  color: white;
}

.chart-container {
  height: 100%;
  margin-bottom: 10vh;
  display: flex;
  align-items: flex-end;
  position: relative;
}

.chart {
  display: flex;
  position: absolute;
  display: flex;
  justify-content: center;
  bottom: $width/2;
}

.columns {
  display: flex;
  align-items: flex-end;
  gap: $width/5;
}

.column-title {
  position: absolute;
  transform: rotateZ(270deg);
  white-space: nowrap;
  transform-origin: 0% 50%;
  bottom: -5px;
  left: 50%;
  color: white;
  text-transform: uppercase;
  font-size: 5vw;
}

.column {
  height: 100px;
  width: $width;
  background: red;
  position: relative;
  border-top-left-radius: $width/2;
  border-top-right-radius: $width/2;
  overflow: hidden;
}

.value-container {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: $width;
  height: $width;
}

.value {
  white-space: nowrap;
  color: white;
  text-transform: uppercase;
  font-size: 5vw;
}

.circle-container {
  display: flex;
  gap: $width/5;
}

.circle {
  width: $width;
  height: $width;
  border-radius: 50%;
}

